import React from 'react'
import { Link } from 'gatsby'

import ClasseBadge from './classe-badge'
import MatterBadge from './matter-badge'

import { Card, CardBody, CardHeader, CardTitle, CardSubtitle } from 'reactstrap'

export const PartPreview = ({ node }) => {
  const Part = () => (
    <Link to={node.slug}>
      Partie {node.nPart}
      :&nbsp;
      {node.part}
    </Link>
  )
  const MatterPart = () => (
    <CardSubtitle>
      <ClasseBadge classe={node.classe} />
      <MatterBadge node={node} />
    </CardSubtitle>
  )

  return (
    <Card className="text-center">
      <CardHeader>
        <CardTitle>
          <Part />
        </CardTitle>
        <CardBody>
          <MatterPart />
        </CardBody>
      </CardHeader>
    </Card>
  )
}
